"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

export default function Home() {
  const [isAdmin, setIsAdmin] = useState(false)
  const [showPassword, setShowPassword] = useState(false)
  const [password, setPassword] = useState("")
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split("T")[0])
  const [results, setResults] = useState<{ [key: string]: string }>({})
  const [adminPassword, setAdminPassword] = useState("paisa@08")
  const [changePasswordMode, setChangePasswordMode] = useState(false)
  const [oldPasswordInput, setOldPasswordInput] = useState("")
  const [newPasswordInput, setNewPasswordInput] = useState("")
  const [confirmPasswordInput, setConfirmPasswordInput] = useState("")
  const [forgotPasswordMode, setForgotPasswordMode] = useState(false)
  const [rememberPassword, setRememberPassword] = useState(false)

  // Generate time slots from 8:30 AM to 6:00 PM with 30-min intervals
  const generateTimeSlots = () => {
    const slots = []
    let time = new Date()
    time.setHours(8, 30, 0)
    const endTime = new Date()
    endTime.setHours(18, 0, 0)

    while (time <= endTime) {
      slots.push(time.toLocaleTimeString("en-US", {
        hour: "numeric",
        minute: "2-digit",
        hour12: true
      }))
      time = new Date(time.getTime() + 30 * 60000)
    }
    return slots
  }

  const timeSlots = generateTimeSlots()

  const couponA = Array.from({ length: 20 }, (_, i) => `A-${i + 1}`)
  const couponB = Array.from({ length: 20 }, (_, i) => `B-${i + 1}`)

  const handleLogin = () => {
    if (password === adminPassword) {
      setIsAuthenticated(true)
      setIsAdmin(false)
      if (rememberPassword) {
        localStorage.setItem("adminPassword", adminPassword)
      }
    } else {
      alert("Incorrect password")
    }
  }

  const handleLogout = () => {
    setIsAuthenticated(false)
    setPassword("")
    setChangePasswordMode(false)
    setForgotPasswordMode(false)
  }

  const handleChangePassword = () => {
    if (oldPasswordInput !== adminPassword) {
      alert("Old password is incorrect")
      return
    }
    if (newPasswordInput !== confirmPasswordInput) {
      alert("New password and confirm password do not match")
      return
    }
    setAdminPassword(newPasswordInput)
    alert("Password changed successfully")
    setChangePasswordMode(false)
    setOldPasswordInput("")
    setNewPasswordInput("")
    setConfirmPasswordInput("")
  }

  const handleForgotPasswordChange = () => {
    if (oldPasswordInput !== adminPassword) {
      alert("Old password is incorrect")
      return
    }
    if (newPasswordInput !== confirmPasswordInput) {
      alert("New password and confirm password do not match")
      return
    }
    setAdminPassword(newPasswordInput)
    alert("Password changed successfully")
    setForgotPasswordMode(false)
    setOldPasswordInput("")
    setNewPasswordInput("")
    setConfirmPasswordInput("")
  }

  const handleResultChange = (key: string, value: string) => {
    setResults((prev) => ({ ...prev, [key]: value }))
  }

  return (
    <main className="container mx-auto p-4">
      <header className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold text-white">PAISA 08</h1>
        {!isAuthenticated && !isAdmin && (
          <Button variant="secondary" onClick={() => setIsAdmin(true)}>
            Admin Login
          </Button>
        )}
        {isAuthenticated && (
          <Button variant="destructive" onClick={handleLogout}>
            Logout
          </Button>
        )}
      </header>

      {!isAuthenticated && isAdmin && (
        <Card className="p-6 max-w-md mx-auto bg-white rounded-lg relative">
          <button
            className="absolute top-2 right-2 text-gray-500 hover:text-gray-700"
            onClick={() => setIsAdmin(false)}
            aria-label="Close"
          >
            âœ•
          </button>
          <h2 className="text-2xl font-semibold mb-4">Admin Login</h2>
          <div className="mb-4">
            <Label htmlFor="password">Password</Label>
            <div className="relative">
              <Input
                id="password"
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                autoFocus
              />
              <button
                type="button"
                className="absolute right-2 top-1/2 -translate-y-1/2 text-gray-600"
                onClick={() => setShowPassword(!showPassword)}
                aria-label="Toggle password visibility"
              >
                {showPassword ? "Hide" : "Show"}
              </button>
            </div>
          </div>
          <div className="mb-4 flex items-center">
            <input
              id="remember"
              type="checkbox"
              checked={rememberPassword}
              onChange={() => setRememberPassword(!rememberPassword)}
              className="mr-2"
            />
            <Label htmlFor="remember">Remember Password</Label>
          </div>
          <Button onClick={handleLogin} className="w-full">
            Login
          </Button>
          <button
            className="mt-4 text-blue-600 underline"
            onClick={() => {
              setForgotPasswordMode(true)
              setIsAdmin(false)
            }}
          >
            Forgot Password?
          </button>
        </Card>
      )}

      {forgotPasswordMode && (
        <Card className="p-6 max-w-md mx-auto bg-white rounded-lg">
          <h2 className="text-2xl font-semibold mb-4">Forgot Password</h2>
          <div className="mb-4">
            <Label htmlFor="oldPassword">Old Password</Label>
            <Input
              id="oldPassword"
              type="password"
              value={oldPasswordInput}
              onChange={(e) => setOldPasswordInput(e.target.value)}
              autoFocus
            />
          </div>
          <div className="mb-4">
            <Label htmlFor="newPassword">New Password</Label>
            <Input
              id="newPassword"
              type="password"
              value={newPasswordInput}
              onChange={(e) => setNewPasswordInput(e.target.value)}
            />
          </div>
          <div className="mb-4">
            <Label htmlFor="confirmPassword">Confirm Password</Label>
            <Input
              id="confirmPassword"
              type="password"
              value={confirmPasswordInput}
              onChange={(e) => setConfirmPasswordInput(e.target.value)}
            />
          </div>
          <Button onClick={handleForgotPasswordChange} className="w-full">
            Change Password
          </Button>
          <Button
            variant="ghost"
            onClick={() => {
              setForgotPasswordMode(false)
              setIsAdmin(true)
            }}
            className="w-full mt-2"
          >
            Back to Login
          </Button>
        </Card>
      )}

      {isAuthenticated && !changePasswordMode && (
        <>
          <Card className="p-4 mb-4">
            <div className="flex gap-4 items-center">
              <Label>Select Date:</Label>
              <Input
                type="date"
                value={selectedDate}
                onChange={(e) => setSelectedDate(e.target.value)}
                className="w-auto"
              />
              <Button
                variant="outline"
                onClick={() => setChangePasswordMode(true)}
                className="ml-auto"
              >
                Change Password
              </Button>
            </div>
          </Card>

          <div className="overflow-x-auto">
            <table className="w-full bg-white rounded-lg border border-gray-300">
              <thead>
                <tr className="bg-gray-100">
                  <th className="border border-gray-300 p-2">Time</th>
                  <th className="border border-gray-300 p-2">Coupon A</th>
                  <th className="border border-gray-300 p-2">Result</th>
                  <th className="border border-gray-300 p-2">Coupon B</th>
                  <th className="border border-gray-300 p-2">Result</th>
                </tr>
              </thead>
              <tbody>
                {timeSlots.map((time, idx) => (
                  <tr key={time} className={idx % 2 === 0 ? "bg-gray-50" : "bg-white"}>
                    <td className="border border-gray-300 p-2 text-center">{time}</td>
                    <td className="border border-gray-300 p-2 text-center">{couponA[idx]}</td>
                    <td className="border border-gray-300 p-2 text-center">
                      <Input
                        type="text"
                        value={results[`A-${idx + 1}-${selectedDate}`] || ""}
                        onChange={(e) => handleResultChange(`A-${idx + 1}-${selectedDate}`, e.target.value)}
                        disabled={!isAuthenticated}
                        placeholder=""
                      />
                    </td>
                    <td className="border border-gray-300 p-2 text-center">{couponB[idx]}</td>
                    <td className="border border-gray-300 p-2 text-center">
                      <Input
                        type="text"
                        value={results[`B-${idx + 1}-${selectedDate}`] || ""}
                        onChange={(e) => handleResultChange(`B-${idx + 1}-${selectedDate}`, e.target.value)}
                        disabled={!isAuthenticated}
                        placeholder=""
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </>
      )}

      {isAuthenticated && changePasswordMode && (
        <Card className="p-6 max-w-md mx-auto bg-white rounded-lg">
          <h2 className="text-2xl font-semibold mb-4">Change Password</h2>
          <div className="mb-4">
            <Label htmlFor="oldPasswordChange">Old Password</Label>
            <Input
              id="oldPasswordChange"
              type="password"
              value={oldPasswordInput}
              onChange={(e) => setOldPasswordInput(e.target.value)}
              autoFocus
            />
          </div>
          <div className="mb-4">
            <Label htmlFor="newPasswordChange">New Password</Label>
            <Input
              id="newPasswordChange"
              type="password"
              value={newPasswordInput}
              onChange={(e) => setNewPasswordInput(e.target.value)}
            />
          </div>
          <div className="mb-4">
            <Label htmlFor="confirmPasswordChange">Confirm Password</Label>
            <Input
              id="confirmPasswordChange"
              type="password"
              value={confirmPasswordInput}
              onChange={(e) => setConfirmPasswordInput(e.target.value)}
            />
          </div>
          <Button onClick={handleChangePassword} className="w-full">
            Change Password
          </Button>
          <Button
            variant="ghost"
            onClick={() => setChangePasswordMode(false)}
            className="w-full mt-2"
          >
            Cancel
          </Button>
        </Card>
      )}
    </main>
  )
}
